          <span> 继承人姓名：</span>
          <span>{{editHeritorAmountInfo.name}}</span>
        </el-col>
      </el-row>
      <el-row style='display: flex!important;align-items: center!important;margin-top: 20px;' :gutter="20">
        <el-col :span="5">
          <span style='color: #bd2c00'>*</span>
          <span style='font-size: medium'> 领取金额：</span>
        </el-col>
