                <el-button :disabled="buttonController[indexnum]" slot="trigger" size="small" type="primary">点击选择{{item.ImportName}}文件</el-button>
                <el-button :disabled="buttonController[indexnum]" style="margin-left: 10px;" size="small" type="success" @click="uploadInheritorfile(importListInfo.Busiid,importListInfo.ChannelCode,item.ImUUid,importListInfo.SeviceCode,indexnum)">上传到服务器</el-button>
                <span style='color: red; margin-left: 10px;' v-if="item.IsReq==1">*必须上传 </span>
                </el-upload>
              </el-row>
              <!-- 图片预览 -->
              <el-row >
                <div v-if="imgUrl[indexnum] != null">
