                  :src="imgUrl[indexnum]" @click="handlePictureCardPreview(imgUrl[indexnum])" title="点击放大图片"/>
                  <el-button slot="trigger" size="small" type="danger" @click="delectInheritorImg(indexnum)">删除图片</el-button>
                </div>
              </el-row>
            </el-row>
