        // console.log(this.uploadImgInfo[index])
        if(this.uploadImgInfo[index] != null){
          let blob = this.base64ImgtoFile(this.uploadImgInfo[index].file,this.uploadImgInfo[index].filename,this.uploadImgInfo[index].type)

          // let blob = new Blob([this.uploadImgInfo[index].file]);   // 返回的文件流数据
          let url = window.URL.createObjectURL(blob);  // 将他转化为路径
          this.imgUrl[index] = url
